import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { OnboardingService } from '../../onboarding.service';

@Component({
  selector: 'lib-exit-confirm',
  templateUrl: './exit-confirm.component.html',
  styleUrls: ['./exit-confirm.component.scss']
})
export class ExitConfirmComponent implements OnInit {

  constructor(@Inject(MAT_DIALOG_DATA) private data: any,
    private dialogRef: MatDialogRef<ExitConfirmComponent>, private service: OnboardingService) { }

  ngOnInit(): void {
  }

  confirm(): void {
    this.dialogRef.close(true);
    this.service.canExit$.next(true)
  }
  cancel(): void {
    this.dialogRef.close(false);
    this.service.canExit$.next(false)
  }

}
