import { ActivatedRoute, Router } from '@angular/router';
import { CellsComponentList, CoreMatTable, displayedColumnsInterface } from "table";
import { Component, OnInit } from '@angular/core';

import { DialogCreateOnboardingComponent } from '../../components/onboarding/dialog-create-onboarding/dialog-create-onboarding.component';
import { MatDialog } from '@angular/material/dialog';
import { OnboardingService } from '../../onboarding.service';

@Component({
  selector: 'lib-onboarding-list',
  templateUrl: './onboarding-list.component.html',
  styleUrls: ['./onboarding-list.component.scss']
})
export class OnboardingListComponent implements OnInit {
  onboardingList: any
  inputSearch = '';
  currentLength = 0;
  currentFilter = 'all'
  public search: string = '';
  onboardingFormatList = []
  onboardingFinished: any
  onboardingToFinish: any
  templates = []
  public columnDefinitions: displayedColumnsInterface[] = [
    {
      key: 'Name',
      value: 'Nom de famille',
      order: 1,
      class: 'u-1',
      sort: true,
    },
    {
      key: 'Id',
      value: 'Position',
      order: 2,
      class: 'u-2',
      sort: true,
    }, {
      key: 'requestor',
      value: 'Demandeur',
      order: 4,
      class: 'u-4',
      sort: true,
    }, {
      key: 'TemplateName',
      value: 'Service',
      order: 5,
      class: 'u-5',
      sort: true,
    },
    {
      key: 'createdAt',
      value: 'Date de création',
      order: 6,
      class: 'u-6',
      module: CellsComponentList.DateFormat,
      sort: true
    },
    {
      key: 'Status',
      value: "Statut",
      order: 7,
      class: 'u-7',
      sort: true,
    },
    {
      key: 'Action',
      value: 'Action',
      order: 8,
      class: 'u-8',
      module: CellsComponentList.ButtonLinkText,
      override: [`/${this.service.mainPath}/onboarding/request`, 'TemplateId', '_id'],
      align: 'center'
    }

  ];
  localService: any;
  public _loading_table = true;
  private HiddenIndex = 0;
  public index = 0;
  lang = 'fr';

  cancelSearch() {
    this.inputSearch = '';
  }

  constructor(
    private service: OnboardingService,
    private router: Router,
    private route: ActivatedRoute,
    private dialog: MatDialog
  ) {
    this.service._templates.subscribe((data) => {
      this.templates = data;
    });
  }

  async ngOnInit() {
    setTimeout(async () => {
      await this.service.getAllOnboarding();
      // await this.service.getAllProfil();
    }, 500);
    this.service.onboarding.subscribe((onboarding) => {
      setTimeout(() => {
        this._loading_table = false;
      }, 2000);

      if (onboarding) {
        let x = onboarding.map((row, index) => {
          const user = row.categories.find((cat) => cat.name === "Utilisateur");
          const formUser = user.forms.map((form) => ({ key: form.key, value: form.value })).reduce((a, v) => ({ ...a, [v.key]: v.value }), {})
          row.Status = row.Finished ? "Terminer" : "A finaliser";
          row.requestor = row?.vcontacts[0]?.Name || '';
          row.Name = row.ContactName = formUser?.LastName + " " + formUser?.FirstName || ""
          row.Profil = formUser?.Type_de_profil_Nowboard__c || ""
          row.Email = formUser?.Email || '';
          row.TemplateName = row.vtemplates.length > 0 ? row.vtemplates[0].Name : '';
          row.Id = index + 1;
          return row;
        })
        this.onboardingFormatList = x;
        this.onboardingList = new CoreMatTable(x,
          {
            active: 'Id', direction: 'desc'
          }, { active: '', valueStart: null, valueEnd: null }, 15, true, true);
        this.currentLength = this.onboardingList.data.length
        this.onboardingFinished = new CoreMatTable(x.filter((data) => data.Finished),
          {
            active: 'Id', direction: 'desc'
          }, { active: '', valueStart: null, valueEnd: null }, 15, true, true);
        this.onboardingToFinish = new CoreMatTable(x.filter((data) => !data.Finished),
          {
            active: 'Id', direction: 'desc'
          }, { active: '', valueStart: null, valueEnd: null }, 15, true, true);
      }
    })
  }

  triggerChange($event) {
    console.log("this.onboardingToFifish", $event);
    if ($event.index === 0) {
      this.currentLength = this.onboardingList.data.length
    } else if ($event.index === 1) {
      this.currentLength = this.onboardingFinished.data.length
    } else if ($event.index === 2) {
      this.currentLength = this.onboardingToFinish.data.length
    }
    if ($event.index !== this.HiddenIndex) {
      this._loading_table = true;
      this.index = $event.index;
      this.router.navigate(
        [],
        {
          relativeTo: this.route,
          queryParams: { page: null, tab: $event.index + 1 },
          queryParamsHandling: 'merge', // remove to replace all query params by provided
        });
      this.HiddenIndex = $event.index
      setTimeout(() => {
        this._loading_table = false;
      }, 2000);
    }
  }

  openDialog() {
    const dialogRef = this.dialog.open(DialogCreateOnboardingComponent, {
      data: {}
    });
    dialogRef.afterClosed().subscribe((current: any) => {
      if (current) {
        this.service.currentOnboarding.next(current);
        this.router.navigate([this.service.mainPath + `/request/${current.TemplateId}/0`])
      }
    });
  }
}
