export function fieldWidth(type: string, length: number, index: number) {
    if (type === 'textarea') {
        if (length === 1) {
            return '100%'
        }
        return '50%'
    } else if (type === 'toggle') {
        return '50%'
    } else if (type === "text" || type == 'select' || type === 'tel' || type === 'email' || type === 'password') {
        if (length < 3) {
            return '50%'
        } else if (length === 3) {
            return '33%'
        } else if (length === 4) {
            return '25%'
        } else if (length === 5) {
            return index <= 2 ? '33%' : '50%'
        } else if (length >= 6) {
            return '33%'
        }
    }
    return '100%'
}