  canDeactivate() {
    if (!this.canExit) {
      this.dialog.open(ExitConfirmComponent, {
        data: ''
      })
      return this.service.canExit$
    }
    return true
  }

  initForm() {
    this.form = this.fb.group({
      Name: ["", Validators.required],
      Picto: [""],
      Description: [""],
      Tags: ["", Validators.required],
      Status: [false, Validators.required],
    });
  }
  async ngOnInit() {
    this.id = this.route.snapshot.paramMap.get("id");

    this.form.valueChanges.subscribe(c => {
      this.canExit = false;
    });

    if (this.id !== "0") {
      setTimeout(async () => {
        this.currentProfil = await this.service.getProfil(this.id);
        this.form.patchValue(this.currentProfil);
        this.categories = this.currentProfil.categories;
      }, 500);
    } else {
      this.showProfileName = true;
      this.categories = this.defaultCategorie;
    }
    setTimeout(async () => {
      this.tags = await this.service.getAllTags();
    }, 500);
    this.getConditionValues();
  }

  onAddNewCategory(e) {
    e.preventDefault();
    this.canExit = false;
    const data = {
      name: "Nom de la catégorie" + this.categories.length,
      editable: true,
      isConditional: false,
      conditionValue: "",
      conditionLocation: {},
      expandOnboarding: true,
      deleteable: true,
      expandMore: true,
      suffixEndpoint: "",
      selector: {
        label: "Company",
        key: "Company",
        defaultValue: [{ value: "Nowteam", checked: true }, { value: "Internal", checked: false }],
        value: "Nowteam"
      },
      ticketType: {
        label: "Type de ticket",
        key: "tycketType",
        defaultValue: [{ value: "Création de compte", checked: true, key: "Creation_de_compte" }, { value: "Configuration de poste", checked: false, key: "Configuration_de_poste" }],
        value: "Creation_de_compte"
      },
      forms: [
        {
          label: "Label à renommer",
          key: "",
          expandOnboarding: true,
          defaultValue: null,
          value: null,
          size: 80,
          isMandatory: true,
          otherValue: '',
          type: "text",
          deleteable: true,
          editable: true,
          isConditional: false,
          conditionValue: "",
          conditionLocation: {},
        },
      ],
    }
    this.categories.push(data);
    this.onHistorics({
      form: data,
      method: 'push',
      location: 'category',
      params: [this.categories.length - 1]
    })
    setTimeout(() => {
      this.scrollToService.scrollTo({
        target: data.name,
        container: 'categories-id',
        duration: 650,
        easing: 'easeOutElastic',
        offset: 20
      });
      setTimeout(() => {
        this.onExpendMore({ field: data }, this.categories.length - 1);
        this.onActiveMenu(data, this.categories.length - 1);
      }, 300);

    }, 200)
  }

