import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { OnboardingService } from '../../../onboarding.service';

@Component({
  selector: 'lib-dialog-create-template',
  templateUrl: './dialog-create-template.component.html',
  styleUrls: ['./dialog-create-template.component.scss']
})
export class DialogCreateTemplateComponent implements OnInit {

  constructor(@Inject(MAT_DIALOG_DATA) private data: any,
    private dialogRef: MatDialogRef<DialogCreateTemplateComponent>, private service: OnboardingService) {

  }
  tags = [];
  profil = {
    Name: '',
    Tags: ''
  }
  ngOnInit(): void {
    this.tags = this.data.tags
    this.profil = this.data?.profil || this.profil;
  }

  onSelect(type): void {
    this.dialogRef.close(type);
  }
  createProfil() {
    if (!this.disabled()) {
      this.dialogRef.close(this.profil)
    }
  }
  close() {
    this.dialogRef.close(false)
  }

  disabled() {
    return !(this.profil.Name !== "" && this.profil.Tags !== "")
  }

}
