import { ActivatedRoute, Router } from "@angular/router";
import { Component, HostListener, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from "@angular/forms";

import { ExitConfirmComponent } from '../../components/exit-confirm/exit-confirm.component';
import { MatDialog } from "@angular/material/dialog";
import { OnboardingService } from "../../onboarding.service";
import { fieldWidth } from "../../utils/utils";

@Component({
  selector: "create-onboarding",
  templateUrl: "./onboarding-create.component.html",
  styleUrls: ["./onboarding-create.component.scss"],
})
export class OnboardingCreateComponent implements OnInit {
  form: FormGroup;
  currentProfil: any = {};
  canExit = true;
  tags = [];
  onboardingId = "0";
  templateId = "0";
  parent = [];
  selectedMenu = 0;
  templates: any;
  contacts: any;
  currentOnboarding: any;
  currentTemplate: any;
  isSelected: boolean;
  currentUser: any;
  currentActive: any;
  showMode = false;
  selectedCategoryIndex = null;
  loading = false;
  invalideValue: boolean;
  detailView: any;
  fieldWidth = fieldWidth
  categories: any
  submitLabel = 'Envoyer ma fiche'
  resumeLabel = 'Reprendre plus tard'
  constructor(
    private fb: FormBuilder,
    public service: OnboardingService,
    private route: ActivatedRoute,
    private router: Router,
    private dialog: MatDialog
  ) {
    this.service._currentOnboarding.subscribe(async (onboarding) => {
      this.onCreateDynamicForm()
      this.isSelected = true;
      if (onboarding) {
        this.form.patchValue({
          TemplateId: onboarding.TemplateId
        });
        await this.onSelect(onboarding.TemplateId, onboarding)
      } else {
        this.templateId = this.route.snapshot.paramMap.get("templateId")
        this.form.patchValue({
          TemplateId: this.templateId
        });
        await this.onSelect(this.templateId, null)
      }
    })
  }

  isObject(obj) {
    return obj !== undefined && obj !== null && obj.constructor == Object;
  }

  canDeactivate() {
    if (!this.canExit) {
      this.dialog.open(ExitConfirmComponent, {
        data: ''
      })
      return this.service.canExit$
    }
    return true
  }

  @HostListener('scroll', ['$event'])
  onScroll(event) {
    const sections = document.querySelectorAll("section");
    const navLi = document.querySelectorAll(".fields ul li");
    let current = "";
    sections.forEach((section) => {
      const sectionTop = section.offsetTop;
      if (event.target.scrollTop > sectionTop - 200) {
        current = section.getAttribute("id")
      }
    });
    navLi.forEach((li) => {
      const id = li.getAttribute('id')
      if (id.substring(0, id.length - 1) === current) {
        li.classList.add("active");
      } else {
        li.classList.remove('active')
      }
    });
  }

  async ngOnInit() {
    this.templateId = this.route.snapshot.paramMap.get("templateId");
    this.onboardingId = this.route.snapshot.paramMap.get("onboardingId");
    if (this.onboardingId && this.onboardingId !== "0") {
      this.showMode = true;
      this.currentOnboarding = await this.service.getOnboarding(this.onboardingId);
      this.form.patchValue(this.currentOnboarding)
      this.categories = this.currentOnboarding.categories;
      this.onLoadViewDetail(this.categories)

      this.currentTemplate = this.currentOnboarding?.vtemplates[0]
      if (this.currentOnboarding.TemplateId) {
        this.isSelected = true;
      }
    }
  }


  onLoadViewDetail(categories) {
    const user = {}
    const categoriesView = categories.map((category) => {
      const { name, expandOnboarding } = category;
      let categoryForm = []
      let fieldCompleted = 0;
      let fieldLength = 0;
      if (category.forms) {
        categoryForm = category.forms.map((form) => {
          if (form.isMandatory) {
            fieldLength += 1;
          }
          if (form.isMandatory && !!form.value) {
            fieldCompleted += 1;
          }
          const newsubforms = {
            label: form.label,
            value: form.type !== "tel" ? form.value : form?.value?.internationalNumber || '',
            key: form.key
          }
          if (category.name === "Utilisateur") {
            user[form.key] = form.value;
          }
          let subForm = []
          if (form.forms) {
            subForm = form.forms.map((subForm) => {
              if (subForm.isMandatory) {
                fieldLength += 1;
              }
              if (subForm.isMandatory && !!subForm.value) {
                fieldCompleted += 1;
              }
              const childforms = {
                label: subForm.label,
                value: subForm.type !== "tel" ? subForm.value : subForm?.value?.internationalNumber || '',
                key: subForm.key
              }

              let child2 = []
              if (subForm.forms) {
                child2 = subForm.forms.map((c2) => {
                  if (c2.isMandatory) {
                    fieldLength += 1;
                  }
                  if (c2.isMandatory && !!c2.value) {
                    fieldCompleted += 1;
                  }
                  return {
                    label: c2.label,
                    value: c2.type !== "tel" ? c2.value : c2?.value?.internationalNumber || '',
                    key: c2.key
                  }
                })
                return {
                  ...childforms,
                  expandOnboarding: subForm.expandOnboarding,
                  forms: child2,
                  key: form.key
                }
              } else {
                return childforms
              }
            })
            return {
              ...newsubforms, expandOnboarding
                : form.expandOnboarding,
              forms: subForm,
              key: form.key
            }
          } else {
            return newsubforms
          }
        })
      }
      return {
        name, expandOnboarding,
        forms: categoryForm,
        key: category.key,
        fieldCompleted,
        fieldLength
      }
    })
    this.detailView = {
      categories: categoriesView,
      // view: this.currentOnboarding.view,
      tickets: this.currentOnboarding?.vtickets,
      createdAt: this.currentOnboarding?.createdAt || new Date().toISOString(),
      user: user,
      Name: this.currentOnboarding?.vtemplates[0]?.Name || this.currentTemplate?.Name
    }

  }

  onCreateDynamicForm() {
    this.form = this.fb.group({
      TemplateId: [0, Validators.required]
    });
  }

  catNameStart(str: string) {
    const vowels = ['a', 'e', 'i', 'o', 'u', 'y'];
    return vowels.includes(str.substr(0, 1).toLocaleLowerCase())
  }

  async onSelect(id, onboarding) {
    if (id) {
      this.currentTemplate = await this.service.getProfil(id);
      this.categories = onboarding ? onboarding.categories : this.currentTemplate.categories
        .filter((cat) => cat.expandOnboarding)
        .map((cat) => {
          if (cat.forms) {
            const f = cat.forms
              .filter((form) => form.expandOnboarding)
              .map((sub) => {
                if (sub.forms) {
                  const s = sub.forms.filter((child) => {
                    if (child.forms) {
                      const c = child.forms.filter((ch) => ch.expandOnboarding)
                      return {
                        ...child,
                        forms: c
                      }
                    }
                    return child;
                  });
                  return {
                    ...sub,
                    forms: s
                  }
                }
                return sub;
              })
            return {
              ...cat,
              forms: f
            }
          }
          return cat
        })
    }
  }

  existSubForm(forms) {
    let exist = false;
    if (forms) {
      for (const sub of forms) {
        if (sub.forms) {
          exist = true;
        }
      }
    }
    return exist
  }

  private scrollToFirstInvalidControl() {
    let form = document.getElementById('form');
    let firstInvalidControl = form.getElementsByClassName('ng-invalid')[0];
    firstInvalidControl.scrollIntoView({ behavior: 'smooth', block: 'center' });
    (firstInvalidControl as HTMLElement).focus();
  }

  onChangeCategory() {
    const checkMandatory = (field) => !!field.value && field.isMandatory && (field.type !== "checkbox_multiple" || field.type !== 'checkbox') || !field.isMandatory || field.type === "checkbox_multiple" || field.type === 'checkbox' || field.type === 'toggle'
    return this.categories.every((cat) => cat.forms.every((field) => {
      if (field.forms) {
        return field.forms.every((form) => {
          if (form.forms) {
            return form.forms.every((child) => {
              return checkMandatory(child)
            })
          }
          return checkMandatory(form)
        })
      }
      return checkMandatory(field)
    }));
  }

  async onSubmit() {
    if (this.form.valid && this.onChangeCategory()) {
      this.loading = true;
      this.submitLabel = "Chargement..."
      const result =
        this.onboardingId && this.onboardingId !== "0"
          ? await this.service.updateOnboarding({
            ...this.form.value,
            categories: this.categories,
            Finished: true,
            _id: this.onboardingId,
            tickets: this.currentOnboarding?.vtickets?.map((ticket) => ticket.Id)
          })
          : await this.service.createOnboarding({
            ...this.form.value,
            categories: this.categories,
            Finished: true,
          });

      if (result) {
        if (this.onboardingId && this.onboardingId !== "0") {
          this.categories = result.categories;
          this.currentOnboarding = result;
        }
        await this.service.getAllProfil();
        this.loading = false;
        this.submitLabel = "Envoyer ma fiche"
        this.canExit = true;
        this.showMode = true;
        this.router.navigate([this.service.mainPath + '/requests']);
      } else {
        console.log("errror......");
        this.loading = false;
        this.submitLabel = "Envoyer ma fiche"
      }
    } else {
      this.scrollToFirstInvalidControl()
    }
  }

  async onResumeLater() {
    this.loading = true;
    this.resumeLabel = "Chargement..."
    const result =
      this.onboardingId && this.onboardingId !== "0"
        ? await this.service.updateOnboarding({
          ...this.form.value,
          categories: this.categories,
          Finished: true,
          _id: this.onboardingId,
          tickets: this.currentOnboarding?.vtickets?.map((ticket) => ticket.Id)
        })
        : await this.service.createOnboarding({
          ...this.form.value,
          categories: this.categories,
          Finished: false,
        });

    if (result) {
      if (this.onboardingId && this.onboardingId !== "0") {
        this.categories = result.categories;
        this.currentOnboarding = result;
      }
      await this.service.getAllProfil();
      this.loading = false;
      this.canExit = true;
      this.showMode = true;
      this.resumeLabel = "Reprendre plus tard"
      this.router.navigate([this.service.mainPath + '/requests']);
    } else {
      console.log("errror......");
      this.loading = false;
      this.resumeLabel = "Reprendre plus tard"

    }
  }

  ingoreDefaultAction(e) {
    e.preventDefault();
  }


  onCheckConditionLocationValue(iscondition, location, conditionValue) {
    if (!iscondition) {
      return true;
    }
    if (location.level === 1) {
      if (this.categories[location.categoryIndex].forms[location.currentIndex].type === 'checkbox_multiple') {
        const array = this.categories[location.categoryIndex].forms[location.currentIndex].defaultValue.filter((check) => check.checked)
        let checked = false;
        for (var i = 0; i < array.length; i++) {
          if (array[i].value == conditionValue) {
            checked = true;
            break;
          }
        }
        return checked;
      } else {
        return this.categories[location.categoryIndex].forms[location.currentIndex].value === conditionValue
      }
    } else if (location.level === 2) {
      if (this.categories[location.categoryIndex].forms[location.formIndex].forms[location.currentIndex].type === 'checkbox_multiple') {
        const array = this.categories[location.categoryIndex].forms[location.formIndex].forms[location.currentIndex].defaultValue.filter((check) => check.checked)
        let checked = false;
        for (var i = 0; i < array.length; i++) {
          if (array[i].value == conditionValue) {
            checked = true;
            break;
          }
        }
        return checked;
      } else {
        return this.categories[location.categoryIndex].forms[location.formIndex].forms[location.currentIndex].value === conditionValue
      }
    } else if (location.level === 3) {
      if (this.categories[location.categoryIndex].forms[location.formIndex].forms[location.subIndex].forms[location.currentIndex].type === 'checkbox_multiple') {
        const array = this.categories[location.categoryIndex].forms[location.formIndex].forms[location.subIndex].forms[location.currentIndex].defaultValue.filter((check) => check.checked)
        let checked = false;
        for (var i = 0; i < array.length; i++) {
          if (array[i].value == conditionValue) {
            checked = true;
            break;
          }
        }
        return checked;
      } else {
        return this.categories[location.categoryIndex].forms[location.formIndex].forms[location.subIndex].forms[location.currentIndex].value === conditionValue
      }
    } else {
      return true
    }

  }

  checkSubForm(forms) {
    let hasSubForm = false;
    for (const form of forms) {
      if (form.forms && form.forms.length > 0) {
        hasSubForm = true;
        break;
      }
    }
    return hasSubForm
  }

  onSelectMenu(i) {
    this.selectedMenu = i;
    this.selectedCategoryIndex = i;
    setTimeout(() => {
      this.selectedCategoryIndex = null;
    }, 2000);
  }

  onCheckValue(event: boolean) {
    this.invalideValue = event;
  }

  onLink(e) {
    e.preventDefault()
  }

  onShowMode() {
    this.showMode = !this.showMode;
    this.onLoadViewDetail(this.categories)
  }

}
